import "./style.scss";

import OverlayContent from "./OverlayContent";
import Portal from "./Portal";
import React from "react";
import Cta from "../../assets/cta.png";
import OutsideClickHandler from "react-outside-click-handler";

import { onCountryClick } from "../Scene3D/AppSignals";

class MobileOverlay extends React.Component {
  constructor(props) {
    super(props);
    this.state = { overlay: false, users: [], country: [] };
    this.openOverlay = this.openOverlay.bind(this);
    this.closeOverlay = this.closeOverlay.bind(this);
  }

  openOverlay() {
    this.setState({ overlay: true });
  }

  closeOverlay() {
    this.setState({ overlay: false });
  }

  onCountryClick = (country, users) => {
    this.openOverlay();
    this.setState({ users: [...users.users], country });
    console.log(users);
  };

  componentDidMount() {
    this.onCountrySignal = onCountryClick.add(this.onCountryClick);
  }

  componentWillUnmount() {
    this.onCountrySignal.detach();
  }

  render() {
    const country = this.state.country;
    const users = this.state.users;

    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          this.closeOverlay();
        }}
      >
        {this.state.overlay && (
          <div className="overlay-accordian">
            <h1>{country}</h1>
            <ul>
              <li>Charlie Charles</li>
              <li>Miranda Mena</li>
              <li>Barbara Barns</li>
            </ul>
            <div className="overlay-container">
              <div className="overlay overlay-anim ">
                <div className="overlay-content-container">
                  <div className="image-container">
                    <img
                      src="https://bitcoinexchangeguide.com/wp-content/uploads/2018/06/Imogen-Heap-Blockchain-Project.jpg"
                      alt="Imogen Heap Profile Picture"
                    />
                  </div>
                  <div className="name-container">
                    <h1>Imogen Heap</h1>
                  </div>

                  <div className="info-container">
                    <p className="vocation">Singer/ Song writer</p>
                    <p className="quote">
                      "Only do what you can see in the headlights."
                    </p>
                    <p className="email">imogen@imogenheap.com</p>
                    <p className="number">+44 7777 777 777</p>
                    <p className="website">imogenheap.com</p>

                    <a href="#" className="viewfull">
                      <img src={Cta} alt="View full passport" />
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </OutsideClickHandler>
    );
  }
}

export default MobileOverlay;
