import "./App.scss";

import { HashRouter, Route } from "react-router-dom";
import React, { Component } from "react";

import Counter from "./components/Counter";
import Overlay from "./components/Overlay";
import MobileOverlay from "./components/MobileOverlay";

import Logo from "./assets/cp-logo-wht.png";
import LogoText from "./assets/cp-text.png";
import Navigator from "./components/Navigator";

import routes from "./routes";
import slugify from "slugify";

class App extends Component {
  render() {
    return (
      <HashRouter basename="/">
        <a href="/">
          <div className="logocontainer">
            <img className="logo" src={Logo} alt="Creative Passport Logo" />
            <img
              className="logoText"
              src={LogoText}
              alt="The Creative Passport"
            />
          </div>
        </a>

        {routes.map(route => {
          const key = slugify(route.path);
          return (
            <Route
              key={key}
              path={route.path}
              exact={route.exact}
              component={route.component}
            />
          );
        })}
        <h3 id="countryName"></h3>
        <Overlay />
      </HashRouter>
    );
  }
}

export default App;
