import React, { Component } from "react";
import slugify from "slugify";

import Switch from "./Switch";

import "./style.scss";

class Navigator extends Component {
  resetCheckboxes = () => {};

  changeRoute = route => {
    const history = this.props;
    history.push(route);
  };

  render() {
    const checkboxes = [
      {
        route: "/location",
        text: "Globe component"
      },
      {
        route: "/location/uk",
        text: "Country component"
      }
    ];

    return (
      <section>
        <div className="container">
          {checkboxes.map((checkbox, i) => {
            const { route, text } = checkbox;
            const key = slugify(text);

            return <Switch key={key} route={route} text={text} />;
          })}
        </div>
      </section>
    );
  }
}

export default Navigator;
