import React from 'react';
import { useHistory } from 'react-router-dom';

const Switch = (props) => {
  const {key, route, text} = props;
  const history = useHistory();
  const changeRoute = (r) => history.push(r);

  return (
    <div key={key} className="switch-wrapper">
      <label className="switch" htmlFor={key} onClick={() => changeRoute(route)}>
        <input type="checkbox" id={key} />
        <div className="slider round"></div>
      </label>
      <p>{text}</p>
    </div>
  )
}

export default Switch;
