import {
  DirectionalLight,
  PointLight,
  Scene,
  Mesh,
  MeshBasicMaterial,
  SphereBufferGeometry
} from "three";
import { GLOBE_RADIUS } from "../consts";

// import ViewCountries from '../views/ViewCountries';
import ViewGlobe from "../views/ViewGlobe";
import ViewParticlesGlobe from "../views/ViewParticlesGlobe";

export default class SceneMain extends Scene {
  init() {
    const directionalLight = new DirectionalLight(0xffffff, 0.5);
    this.add(directionalLight);

    this.tick = 0;

    this.sphere = new SphereBufferGeometry(10, 16, 8);

    // this.light1 = new PointLight(0xff0040, 20, 100);
    //
    // this.light1.add(
    //   new Mesh(this.sphere, new MeshBasicMaterial({ color: 0xff0040 }))
    // );

    // this.add(this.light1);

    this.viewParticlesGlobe = new ViewParticlesGlobe(); // just the particles
    this.add(this.viewParticlesGlobe);

    this.viewGlobe = new ViewGlobe(); // the globe underneath
    this.add(this.viewGlobe);

    // this.viewCountries = new ViewCountries();
    // this.add(this.viewCountries);
  }

  update() {
    this.viewParticlesGlobe.rotation.y += 0.003;

    // this.tick++;
    //
    // this.light1.position.x = Math.cos(this.tick / 20) * (GLOBE_RADIUS + 20);
    // this.light1.position.z = Math.sin(this.tick / 20) * (GLOBE_RADIUS + 20);
  }

  pushParticlesUp(intersection) {
    this.viewParticlesGlobe.pushParticlesUp(intersection); // will ultimately update the shader of the particles
  }
  onHoverCountry(countryId, intersection) {
    this.viewParticlesGlobe.update(countryId); // will ultimately update the shader of the particles
  }

  unmount() {}
}
